<template>
    <div class="app">
        <div class="signin">
            <img src="@/static/images/login/logo@2x.png" alt class="logo" />
            <ul class="list">
                <li>
                    <img src="@/static/images/login/login_user_icon.png" alt="" />
                    <input type="text" placeholder="请输入手机号" v-model="loginform.username" />
                </li>
                <li>
                    <img src="@/static/images/login/login_password_icon.png" alt="" />
                    <input type="password" placeholder="请输入密码" v-model="loginform.password" />
                </li>
            </ul>
            <div class="login_box"><button @click="loginSubmit">登录</button></div>
            <div class="fast_reg"><router-link to="/register">快速注册</router-link></div>
        </div>
    </div>
</template>

<script>
    import { toast, Dialog } from 'vant';
    export default {
        name: "Login",
        data()
        {
            return {
                loginform:{
                    username:'',
                    password:'',
                    code:''
                },
                redirect:''
            }
        },
        watch: {
            $route: {
                handler: function(route) {
                    this.redirect = route.query && route.query.redirect
                },
                immediate: true
            }
        },
        methods:{

            loginSubmit()
            {
                let form = this.loginform
                if (!form.username || form.username == ''){
                    toast('请填写手机号')
                    return
                }
                var reg=/^1[3456789]\d{9}$/;
                if(!reg.test(form.username)){
                    toast('请填写有效手机号')
                    return;
                }
                if (!form.password || form.password == ''){
                    toast('请填写密码')
                    return
                }
                this.login()
            },
            login()
            {
                this.$store.dispatch('user/login', this.loginform).then((res) => {

                    if (res.error)
                    {
                        toast(res.msg)
                        return
                    }
                    if (this.redirect){

                        this.$router.push({ path: this.redirect })
                    } else
                    {
                        this.$router.push({ path: '/' })
                    }


                }).catch(() => {

                })
            }
        }
    }
</script>

<style scoped>
    @import "../../static/css/login.css";
</style>



